<template>
  <div class="modal fade" :class="{'is-open': open}">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <a @click="$emit('close')" class="close">
            <i class="icon-close"></i>
          </a>
          <h4 class="modal-title">預覽窗格</h4>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <div class="radio radio-inline">
              <input type="radio" v-model="printType" id="print-type-1" value="1">
              <label for="print-type-1">郵政局格式</label>
            </div>
            <div class="radio radio-inline">
              <input type="radio" v-model="printType" id="print-type-2" value="2">
              <label for="print-type-2">電話簿</label>
            </div>
          </div>
          <div id="pdf-container" class="pdf-preview"
          :class="{
            'section-loading': loading_1
          }">
            <div id="pdfDiv" class="scrollBar">
              <pdf 
              v-for="i in pageCount"
              :key="i"
              :src="pdfContent" 
              :page="i"
              ref="myPdfComponent"
              style="display: inline-block; width: 100%"
              ></pdf>
            </div>
          </div>
          <div class="action text-right">
            <a class="btn btn-default" v-on:click="openPDF()" :disabled="loading_1">開啟PDF</a>
            <a class="btn btn-primary" @click="doPrint();initPrintModal()" :disabled="loading_1">列印</a>
            <!-- <a class="btn btn-primary" @click="$refs.myPdfComponent.print()" :disabled="loading_1">列印</a> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getPostalPDF } from '../../service';
import { getPhonePDF } from '../../service';
import pdf from 'vue-pdf';
import { log } from 'util';


export default {
  name: 'PrintModal',
  components:{
    pdf
  },
  props: {
    open: {
      type: Boolean,
      default: false
    },
    custs: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      loading_1: false,
      printType: 1,
      pdfContent: '',
      pdfOrgData: [], 
      pageCount: 0, 
      currentPage: 0
    }
  },
  computed: {
    isIE () {
      var ua = window.navigator.userAgent;

      // Test values; Uncomment to check result …

      // IE 10
      // ua = 'Mozilla/5.0 (compatible; MSIE 10.0; Windows NT 6.2; Trident/6.0)';
      
      // IE 11
      // ua = 'Mozilla/5.0 (Windows NT 6.3; Trident/7.0; rv:11.0) like Gecko';
      
      // Edge 12 (Spartan)
      // ua = 'Mozilla/5.0 (Windows NT 10.0; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/39.0.2171.71 Safari/537.36 Edge/12.0';
      
      // Edge 13
      // ua = 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/46.0.2486.0 Safari/537.36 Edge/13.10586';

      var msie = ua.indexOf('MSIE ');
      if (msie > 0) {
        // IE 10 or older => return version number
        return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
      }

      var trident = ua.indexOf('Trident/');
      if (trident > 0) {
        // IE 11 => return version number
        var rv = ua.indexOf('rv:');
        return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
      }

      var edge = ua.indexOf('Edge/');
      if (edge > 0) {
        // Edge (IE 12+) => return version number
        return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
      }
      // other browser
      return false;
    }
  },
  computed: {
    isIE () {
      var rv;
      var ie = false
      var ua = navigator.userAgent;
      if (new RegExp("Trident/.*rv:([0-9]{1,}[\.0-9]{0,})").exec(ua) != null) {
        rv = parseFloat(RegExp.$1);
      } else {
        rv = -1;
      }

      if(ua.indexOf("MSIE") != -1) {
        ie = true
      }

      return ie || rv != -1
    }
  },
  watch: {
    custs : 'initPrintModal',
    open : 'initPrintModal',
    printType : 'initPrintModal'
  },
  methods: {
    clonePDF (oldCanvas) {
      if(!this.isIE) {
        var newCanvas = document.createElement('canvas');
        //set dimensions
        newCanvas.width = oldCanvas.width;
        newCanvas.height = oldCanvas.height;
        //apply the old canvas to the new one
        newCanvas.getContext('2d').drawImage(oldCanvas, 0, 0);
        //return the new canvas
        // console.log(newCanvas)
        return newCanvas;
      } else {
        var image = new Image();
        image.src = oldCanvas.toDataURL("image/png");
        return image
      }
    },
    doPrint () {
      let canvas = this.$el.querySelector("#pdfDiv").querySelectorAll('canvas')
      var mywindow = window.open('', 'PRINT', `height=${canvas.width},width=${canvas.height}`);
      console.log(`isIE: ` + this.isIE)
      mywindow.document.write('<html><head><title>' + document.title  + '</title>');
      mywindow.document.write('</head><body >');

      if(this.isIE) {
        for(var i in canvas) {
          if(typeof canvas[i] ==='object') {
            let pdf = this.clonePDF(canvas[i])
            mywindow.document.write(pdf.outerHTML)
          }
        }
      }

      mywindow.document.write('</body></html>');

      
      if(!this.isIE) {
        canvas.forEach(c => {
          let pdf = this.clonePDF(c)
          mywindow.document.body.appendChild(pdf)
        })
      } 
      
      mywindow.document.close();

      setTimeout(function() {
        mywindow.focus();
        mywindow.print();
      })
      // this.initPrintModal()
      return false;
    },
    initPrintModal() {
      if(this.open && this.printType == 1) {
        this.loading_1 = true
        getPostalPDF(this.$store.state.userInfo.id, this.custs).then(result => {
          this.pdfOrgData = [result]
          this.pdfContent = new Uint8Array(result)
          this.loading_1 = false
          let loadingTask = pdf.createLoadingTask(this.pdfContent);

          loadingTask.then(pdf => {
            this.pageCount = pdf.numPages;
          });
          //window.URL.revokeObjectURL(fileURL)
        })
      } else if (this.open && this.printType == 2) {
        this.loading_1 = true
        getPhonePDF(this.$store.state.userInfo.id, this.custs).then(result => {
          let file = new Blob([result], {type: "application/pdf"})
          let fileURL = window.URL.createObjectURL(file);
          this.pdfContent = fileURL
          this.loading_1 = false
        })
      }
    },
    openPDF() {
        if(this.printType == 1){
          let file = new Blob(this.pdfOrgData, {type: "application/pdf"})
          if(window.navigator.msSaveOrOpenBlob) {
            window.navigator.msSaveOrOpenBlob(file, 'test.pdf')
          } else {
            let fileURL = window.URL.createObjectURL(file);
            window.open(fileURL);
          }
        }else if(this.printType == 2){
          window.open(this.pdfContent);
        }
        
    },
    printStuff(){
      // let frm = document.getElementById('printPdf').contentWindow;
      // frm.focus();
      // frm.print();
      // return false;
      this.$refs.myPdfComponent[0].print()
      //this.htmlToPaper('pdfDiv');
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-body{
  iframe{
    width: 100%;
    height: 100%;
    min-height: 500px;
  }
}
.modal.fade .pdf-preview{
  height: initial;
}

.scrollBar {
  overflow: auto;
  height: 418px;
}
</style>
